@use "tokens" as *;

body {
  background-color: #1e1e28;
  font-family: "Source Code Pro", monospace;
  font-optical-sizing: auto;
  font-style: normal;
}

.resume-doc {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #a6a5ae;

  a,
  h1 {
    color: #f9feff;
    margin: 0;
  }

  .url-wrapper {
    word-break: break-all;
  }

  b {
    font-weight: bolder;
  }

  h2 {
    color: #1e1e28;
    text-transform: uppercase;
    background: #5f5f72;
    background: linear-gradient(90deg, #5f5f72 10%, rgb(166, 165, 174, 0) 100%);
    padding: 0.5rem 1rem;
    border-radius: 8px 0 0 8px;
  }

  h3 {
    color: $color-primary;
  }

  h4 {
    color: $color-primary;
  }

  &__header {
    display: flex;
    flex-direction: column;
    background-image: url(./img/banner.png);

    &__avatar {
      display: flex;
      justify-content: center;
      padding: 2rem;

      .profile-pic {
        border: 1px solid #333;
        width: auto;
        height: auto;

        img {
          width: 80vw;
          max-width: 12rem;
        }
      }
    }

    &__description {
      background: #2a2a3b;
      padding: 2rem 3rem;

      ul {
        list-style: none;
        padding: 0;
      }
    }
  }

  &__content {
    padding: 2rem;
    box-sizing: border-box;
  }

  .content-experience {
    box-sizing: border-box;
    border-bottom: 3px solid #a6a5ae5e;

    &--job {
      width: 100%;
      white-space: normal;
      overflow-wrap: break-word;
      margin-bottom: 3rem;
      padding-left: 1rem;
      box-sizing: border-box;
    }
  }
}

@media (min-width: 1024px) {
  .resume-doc {
    flex-direction: row;
    align-items: flex-start;

    &__header {
      width: 30%;
      height: 100vh;
      background: #2a2a3b;
      position: sticky;
      top: 0;
      align-self: flex-start;
    }

    &__content {
      width: 70%;
    }
  }
}
